import React, { Component } from 'react';
import { connect } from 'react-redux';

import Chips from '../customFields/chips';
import InputField from '../customFields/inputField';
import { Endpoint } from '../../utils/constants';
import { updatePageData } from '../../actions/page';

class CheckinMember extends Component {
    constructor(props) {
        super(props);
        this.state = {
            memberId: '',
            memberData: {},
            withGuest: '',
            isSaving: false,
        }
    }

    onSubmit = (e) => {
        // alert(typeof parseInt(this.state.withGuest))
        e.preventDefault();
        if (this.state.memberId === '') {
            this.setState({ error: 'No Memebr Selected' });
            return;
        }
        if ( parseInt(this.state.withGuest) <= 0) {
            this.setState({ errorGuest: 'Enter Valid Input' });
            return;
        }
        this.setState({ isSaving: true });
        let payload = Object.assign({}, this.state);
        payload.token = this.props.token
        delete payload.memberData;
        this.props.updatePageData({
            payload,
            url: `${Endpoint.CHECKIN_MEMBER}`,
            method: "POST"
        }, (response) => {
            if (response.status) {
                this.setState({ error: '' , errorGuest: '', isSaving:false });
                this.props.toastmessage(response.message);
                this.props.closeModal();
            } else {
                this.setState({ error: response.message, errorGuest: response.message, isSaving:false })
            }
        });
    }

    inputChange = (e, id) => {
        let {
            target: { name, value }
        } = e;
        if(name === 'withGuest' && value && value > 4){
            this.setState({errorGuest:  'Maximum 4 guests are allowed.'})
            return;
        }
        this.setState({ [name]: value, errorGuest: '' });
    };
    setChips = (value, name, item = {}) => {
        this.setState({ [name]: value, memberId: value && value.length ? value[0].id : '' });
    };
    
    render() {
        let { memberId, memberData, withGuest, error,errorGuest, isSaving } = this.state;
        let { token } = this.props;
        return (
            <div className='inviteGuests checkinModal'>
                <div className='_title'>CHECK IN A MEMEBR</div>
                <div className='row no-gutters'>
                    <div className='col'>
                        <Chips
                            placeholder="Member Name or Member No."
                            inputClassName="w100per  form_control"
                            name="memberData"
                            namespace="countryList"
                            livesearch={true}
                            autocomplete={false}
                            isSingle={true}
                            service={`${Endpoint.MEMBER}?token=${token}`}
                            vk="id"
                            sk="name"
                            chips={memberData}
                            maxCount={1}
                            setChips={this.setChips}
                        />
                        {/* {!memberId && <span className='search-icon'><i className='fa fa-search'></i></span>} */}
                        {error && <p className="error_msg mt5"> {error}</p>}
                        <div>
                            <InputField name="withGuest" groupClass="inviteNature mt20" value={withGuest}
                                inputChange={this.inputChange} dom={{ placeholder: 'With Guest', type: 'number'}} />
                        </div>
                        {errorGuest && <p className="error_msg mt5"> {errorGuest}</p>}
                    </div>
                    {memberId ?
                    <div className='col-4'>
                        {memberId &&
                            <div className='image-style upload-photo' style={{ backgroundImage: `url(${memberData[0].image})` }}>
                                <img src={'./images/imagesize2_3.png'} className='w100per' alt="b"/>
                            </div>
                        }
                        {memberId &&
                            <div className='guest-detail ml20'>
                                <div className='text-center'>
                                    <p className='txt-member'>{memberData[0].name}</p>
                                    <p className='txt-member'>M No. - {memberData[0].mno}</p>
                                </div>
                            </div>
                        }
                    </div> : ''
                    }
                </div>
                <div className='row'>
                    <div className='col text-center' onClick={this.onSubmit}><span className={`btn send-btn mt15 ${isSaving ? 'btndisabled' : ''}`}>CHECK IN</span></div>
                    
                </div>
            </div>
        );
    }
}
function mapStateToProps({ auth: { token } }) {
    return { token }
}

export default connect(mapStateToProps, { updatePageData })(CheckinMember);
